
.content { 
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    z-index: 999;
    width: 320px;
    padding: 30px 32px;
    background: rgba(255, 255, 255, 0.04);
    color: rgb(22, 25, 26);
    box-shadow: 5px 5px 5px 5px;
    border-radius: 25px;
    border: none;
    outline: none;
    

}

.text { 
    color:rgb(148, 135, 135);
    box-sizing: border-box;
    border: 1px solid;
    border-radius: 5px;
    font-size: 20px;
    margin: 10px;
    background-color: rgba(97, 62, 62, 0.066);
    padding: 5px;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-decoration: none;
}
 .text:hover{
    
    background-color:rgba(120, 133, 131, 0.7);
    color: black;
} 
.button{
	/* background: #d3cdcb; */
    background-color:rgba(120, 133, 131, 0.7);
    color: black;
	/* border: 1px solid rgb(198, 220, 137);
	color: rgb(135, 183, 154); */
	font-size: 25px;
	letter-spacing: 5px;
    padding: 2px;
    width: 150px;
	cursor: pointer;
    border-radius: 45px;
    border: none;
    outline: none;
    
	
}
.button:hover{
    background-color: rgba(46, 63, 60, 0.7);
}

.buttons button{
    background-color:rgba(120, 133, 131, 0.7);
    color: black;
    color:rgb(70, 54, 54);
    background: rgba(57, 63, 52, 0.748);
    border: 1px solid rgb(18, 17, 17);
    margin-top: 10px;  
    font-size: 20px;
    padding: 5px;
    letter-spacing: 2px;
    width: 80%;
    border-radius: 25px;
    border: none;
    outline: none;
}

.buttons button:hover{
    
    color:black;
    /* background-color: rgba(38, 176, 153, 0.7); */
}


